import React, { LegacyRef, MutableRefObject } from 'react';
import { render } from 'react-dom';
import {
	Stage,
	Layer,
	Star,
	Text,
	Label,
	Tag,
	Image as KonvaImage,
} from 'react-konva';
import Konva from 'konva';
import * as R from 'ramda';
import useImage from 'use-image';

const TESTTEXTS = '';
const TESTTEXTL = ` - 108
- - 
- - 
- - 
- -   
- - 
- -  
- - 
- -    `;

const URL = 'https://i.imgur.com/OXYO4K4.png';

export default function MyAppRoot() {
	const [state_origImageW, set_origImageW] = React.useState<number>(100);
	const [state_origImageH, set_origImageH] = React.useState<number>(100);
	React.useEffect(() => {
		(async () => {
			await setOrigImageSize();
		})();
	}, []);
	const setOrigImageSize = async () => {
		try {
			const res = await loadImage(URL);
			set_origImageW(res.height);
			set_origImageH(res.width);
			console.log(['hyou!', res.width, res.height]);
		} catch (e) {
			console.log('hyou! onload error', e);
		}
	};

	return (
		<Canvas
			imageUrl={URL}
			origImageH={state_origImageH}
			origImageW={state_origImageW}
		/>
	);
}

const Canvas = (props: {
	imageUrl: string;
	origImageW: number;
	origImageH: number;
}) => {
	const [state_stageH, set_stageH] = React.useState<number>(props.origImageH);
	const [state_stageW, set_stageW] = React.useState<number>(props.origImageW);
	const [state_textH, set_textH] = React.useState<number>(10);
	const [state_textW, set_textW] = React.useState<number>(10);
	const [state_imageH, set_imageH] = React.useState<number>(10);
	const [state_imageW, set_imageW] = React.useState<number>(10);
	const ref_text = React.useRef<Konva.Text>(null);
	const ref_image = React.useRef<Konva.Image>(null);
	const ref_timer = React.useRef<NodeJS.Timeout>();
	// set crossOrigin of image as second argument
	const [state_image, set_imageStatus] = useImage(URL, 'anonymous');

	React.useEffect(() => {
		ref_timer.current = setInterval(() => interval(), 2000);
		return () => ref_timer.current && clearInterval(ref_timer.current);
	}, []);

	React.useEffect(() => {
		set_stageH(state_textH);
		set_stageW(state_textW);
		const newImageSize = calcFitImageSizeToStage(
			state_textH,
			state_textW,
			props.origImageH,
			props.origImageW,
		);
		set_imageH(newImageSize.height);
		set_imageW(newImageSize.width);
	}, [state_textW, state_textH, props.origImageH, props.origImageW]);

	const interval = () => {
		console.log('timer working');
		set_textW(R.defaultTo(10, ref_text.current?.width()));
		set_textH(R.defaultTo(10, ref_text.current?.height()));
	};

	const img = new Image();
	img.src = URL; // 

	const testText = 'a';

	console.log({
		textwidth: ref_text.current?.width(),
		textmeasure: ref_text.current?.measureSize(testText),
		imageref: ref_image.current?.size(),
		imageorigsize: { H: props.origImageH, W: props.origImageW },
		imagecurrentsize: { H: state_imageH, W: state_imageW },
	});

	//set_stageW(R.defaultTo(10, ref_text.current?.width()));
	//set_stageH(R.defaultTo(10, ref_text.current?.height()));
	return (
		<Stage width={state_stageW} height={state_stageH}>
			<Layer>
				<KonvaImage
					image={state_image}
					width={state_imageW}
					height={state_imageH}
					opacity={0.5}
					ref={ref_image}
					//draggable={true}
				/>
				<KonvaImage
					image={state_image}
					width={state_imageW}
					height={state_imageH}
					y={state_imageH}
					opacity={0.5}
					ref={ref_image}
					//draggable={true}
				/>
				<KonvaImage
					image={state_image}
					width={state_imageW}
					height={state_imageH}
					y={state_imageH * 2}
					opacity={0.5}
					ref={ref_image}
					//draggable={true}
				/>
				<Text
					ref={ref_text}
					text={TESTTEXTL}
					fill='green'
					fontSize={30}
					width={500}
				/>
			</Layer>
		</Stage>
	);
};
type t_imageSize = { width: number; height: number };
const calcFitImageSizeToStage = (
	stageH: number,
	stageW: number,
	imageH: number,
	imageW: number,
): t_imageSize => {
	const hw = { height: imageH, width: imageW } as const;
	const ratio = stageH > stageW ? stageW / imageW : stageH / imageH;
	return R.map<t_imageSize, t_imageSize>(R.multiply(ratio), hw);
};

const loadImage = (src: string): Promise<HTMLImageElement> => {
	console.log('1');
	return new Promise((resolve, reject) => {
		console.log('2');
		const img = new Image();
		img.onload = () => resolve(img);
		img.onerror = (e) => reject(e);
		img.src = src;
	});
};
